.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App {
  background-color: #f1f1f1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
iframe {
  width: 100%;
  min-width: 304px;
  max-width: 620px;
  height: 330px;
  border: none;
}
._se_tab_content_image>.se-dialog-body>.se-dialog-form:nth-of-type(1) {
  display: none !important;
}
.ant-input{
  border-radius: 0;
}
.ant-seclect-selection{
  border-radius: 0 !important;
}